<template>
	<div class="farm-area-num-18" v-loading="loading" element-loading-spinner="iconfont icon-loading1" element-loading-background="rgba(0, 0, 0, 0.2)">
		<area-panel area-title="在田地块统计" @hand-sub-title="handlePage">
			<div class="panel-wrapper" v-show="!loading" slot="panelMain">
				<div class="top-box">
					<div class="left-icon"></div>
					<div class="right-info">
						<div class="title">在田面积</div>
						<div class="content">{{ fieldAreaTotal }}<span>万亩</span></div>
					</div>
				</div>
				<div class="bottom-box">
					<div class="item">
						<p>
							种植业在田面积
							<el-tooltip key="planting" effect="dark" popper-class="custom-tooltip-panel-item" placement="top" :visible-arrow="false" :popper-options="{removeOnDestroy: true}">
								<div v-if="planting" slot="content" class="tooltip-box">
									<div class="bg-style"></div>
									<p>
										入网在田面积占比：<span class="text-style">{{ calcPercent(planting) }}</span>
									</p>
									<p>
										入网地块总面积：<span class="text-style">{{ planting.productionLandArea }}万亩</span>
									</p>
								</div>
								<i class="iconfont icon-tishi1" @click="buttonClick"></i>
							</el-tooltip>
						</p>
						<p v-if="planting">{{ planting.fieldArea || 0 }}<span>万亩</span></p>
					</div>
					<div class="item">
						<p>
							蔬菜在田面积
							<el-tooltip key="vegetables" effect="dark" popper-class="custom-tooltip-panel-item" placement="top" :visible-arrow="false" :popper-options="{removeOnDestroy: true}">
								<div v-if="vegetables" slot="content" class="tooltip-box">
									<div class="bg-style"></div>
									<p>
										入网在田面积占比：<span class="text-style">{{ calcPercent(vegetables) }}</span>
									</p>
									<p>
										入网地块总面积：<span class="text-style">{{ vegetables.productionLandArea }}万亩</span>
									</p>
								</div>
								<i class="iconfont icon-tishi1" @click="buttonClick"></i>
							</el-tooltip>
						</p>
						<p v-if="vegetables">{{ vegetables.fieldArea || 0 }}<span>万亩</span></p>
					</div>
					<div class="item">
						<p>
							渔业在田面积
							<el-tooltip key="fisheries" effect="dark" popper-class="custom-tooltip-panel-item" placement="top" :visible-arrow="false" :popper-options="{removeOnDestroy: true}">
								<div v-if="fisheries" slot="content" class="tooltip-box">
									<div class="bg-style"></div>
									<p>
										入网在田面积占比：<span class="text-style">{{ calcPercent(fisheries) }}</span>
									</p>
									<p>
										入网地块总面积：<span class="text-style">{{ fisheries.productionLandArea }}万亩</span>
									</p>
								</div>
								<i class="iconfont icon-tishi1" @click="buttonClick"></i>
							</el-tooltip>
						</p>
						<p v-if="fisheries">{{ fisheries.fieldArea || 0 }}<span>万亩</span></p>
					</div>
				</div>
			</div>
		</area-panel>
	</div>
</template>
<script>
import areaPanel from '@/components/overview/area-panel';
import {areaLevelCorAreaId, maxDecimalPlaces} from '@/common/fn';
export default {
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		}
	},
	data() {
		return {
			loading: true,
			planting: null, // 种植业
			vegetables: null, // 蔬菜
			fisheries: null, // 渔业
			fieldAreaTotal: 0 //在田地块总面积
		};
	},
	mounted() {
		this.initMoudle();
	},
	methods: {
		handlePage() {
			// this.$router.push({
			// 	name: 'fieldsStatisticsList'
			// });
			this.$emit('handleClick', 'fieldsStatisticsList', '在田地块统计');
		},
		initMoudle() {
			this.planting = null;
			this.vegetables = null;
			this.fisheries = null;
			this.fieldAreaTotal = null;
			this.getPlotByArea();
		},
		getPlotByArea() {
			this.$ajax
				.post(this.$api.overview.getPlotByTypeAndArea, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType,
					type: 0
				})
				.then(({data}) => {
					// console.log('在田地块统计', data);
					if (data.respCode === 0) {
						data.obj.forEach(item => {
							switch (item.type) {
								case 1:
									this.planting = item;
									break;
								case 2:
									this.vegetables = item;
									break;
								case 3:
									this.fisheries = item;
									break;
								case 0:
									this.fieldAreaTotal = item.fieldArea;
							}
						});
						this.loading = false;
					}
				})
				.catch(e => {
					console.log('在田地块统计 error: ', e);
				});
		},
		calcPercent({fieldArea, productionLandArea}) {
			if (fieldArea === 0 || productionLandArea === 0) {
				return '- -';
			}
			return (
				maxDecimalPlaces({
					value: (fieldArea / productionLandArea) * 100
				}) + '%'
			);
		},
		buttonClick(e) {
			e.target.blur();
		}
	},
	components: {areaPanel}
};
</script>
<style lang="scss" scoped>
.farm-area-num-18 {
	width: 100%;
	height: 100%;
	.panel-wrapper {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.top-box {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 90%;
		height: 26%;
		min-height: 109px;
		border: 1px solid #10a4a9;
		border-radius: 55px;
		margin: 5% auto 0;
		background: #0a2033;
		box-shadow: 0px 0px 30px 0px rgba(2, 196, 252, 0.45) inset;
		.left-icon {
			width: 84px;
			height: 84px;
			margin-left: 14px;
			background: url('../../../assets/images/overview/farm-area-icon.png') no-repeat;
		}
		.right-info {
			display: flex;
			flex-direction: column;
			justify-content: center;
			flex: 1;
			margin-left: 12.3%;
			.title {
				font-size: 16px;
				font-weight: bold;
				color: #9dd1d7;
				line-height: 14px;
			}
			.content {
				font-size: 30px;
				font-weight: bold;
				color: #feeb48;
				line-height: 19px;
				margin-top: 15px;
				span {
					font-size: 18px;
				}
			}
		}
	}
	.bottom-box {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex: 1;
		width: 92%;
		// margin: 8% auto 0;
		> div {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			text-align: center;
			p:nth-child(1) {
				width: 100%;
				font-size: 12px;
				color: #9dd1d7;
			}
			p:nth-child(2) {
				width: 100%;
				// font-size: 18px;
				font-size: 20px;
				color: #00f3fc;
				margin: 15px 0 13px;
				text-align: left;
				span {
					font-size: 14px;
				}
			}
		}
		i.iconfont {
			color: #038794;
		}
	}
	/deep/ .el-loading-spinner {
		overflow: hidden;
		.iconfont {
			display: block;
			font-size: 40px;
			color: #00f6ff;
			animation: loading 1.25s linear infinite;
			margin-top: 24px;
			margin-left: 8px;
		}
	}
}

.custom-tooltip-panel-item {
	box-shadow: none !important;
	.tooltip-box {
		box-sizing: border-box;
		position: relative;
		padding: 9px 30px 20px;
		font-size: 14px;
		color: #6dc1cb;
		.bg-style {
			position: absolute;
			top: -10px;
			left: -10px;
			right: -10px;
			bottom: -20px;
			z-index: 0;
			background: url('../../../assets/images/overview/bg-tooltip.png') no-repeat;
			background-size: 100% 100%;
			box-sizing: border-box;
		}
		> p {
			position: relative;
			margin-top: 10px;
			.text-style {
				color: #00f3fc;
				font-weight: bold;
			}
		}
	}
}

@media screen and (min-width: 3000px) {
	.farm-area-num-18 {
		.top-box {
			.right-info {
				.title {
					font-size: 24px;
				}
				.content {
					font-size: 43px;
					margin-top: 25px;
					span {
						font-size: 26px;
					}
				}
			}
		}
		.bottom-box {
			> div {
				p:nth-child(1) {
					font-size: 18px;
				}
				p:nth-child(2) {
					font-size: 26px;
					span {
						font-size: 18px;
					}
				}
			}
		}
	}
	.farm-area-num-18 .top-box[data-v-4c686b6f] {
		margin: 3% auto 0;
	}
}
</style>



